
@-webkit-keyframes fadeInCloud {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
@keyframes fadeInCloud {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
.c-cloud {
  width: 100%;
  height: 100%;
  position: relative;
}
.c-cloud.back .m-front {
    -webkit-transition: all .4s;
    transition: all .4s;
    opacity: 0;
}
.c-cloud.back .m-back {
    opacity: 0;
    -webkit-transition: all .4s .8s;
    transition: all .4s .8s;
}
.c-cloud .u-mask {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 0;
}
.c-cloud .m-back, .c-cloud .m-front {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.c-cloud .m-back {
    -webkit-animation: fadeInCloud .4s;
            animation: fadeInCloud .4s;
    font-size: 0;
    padding: 80px 30px;
    text-align: justify;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    pointer-events: none;
    z-index: 1;
}
.c-cloud .m-back:after {
      width: 100%;
      height: 1px;
      content: '';
      display: inline-block;
}
.c-cloud .m-back .u-photo {
      margin-bottom: 35px;
      display: inline-block;
      width: 322px;
      height: 322px;
      -webkit-backface-visibility: hidden;
              backface-visibility: hidden;
      position: relative;
      -webkit-transform-style: preserve-3d;
              transform-style: preserve-3d;
      -webkit-perspective: 1000px;
              perspective: 1000px;
}
.c-cloud .m-back .u-photo .u-front, .c-cloud .m-back .u-photo .u-back {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        overflow: hidden;
        -webkit-transform-style: preserve-3d;
                transform-style: preserve-3d;
}
.c-cloud .m-back .u-photo .u-front {
        z-index: 2;
        -webkit-transition: -webkit-transform .4s .4s linear;
        transition: -webkit-transform .4s .4s linear;
        transition: transform .4s .4s linear;
        transition: transform .4s .4s linear, -webkit-transform .4s .4s linear;
}
.c-cloud .m-back .u-photo .u-front img {
          position: absolute;
          height: 100%;
          display: block;
          left: 50%;
          top: 50%;
          -webkit-transform: translate(-50%, -50%);
                  transform: translate(-50%, -50%);
}
.c-cloud .m-back .u-photo .u-back {
        background-color: black;
        -webkit-transition: -webkit-transform .4s linear;
        transition: -webkit-transform .4s linear;
        transition: transform .4s linear;
        transition: transform .4s linear, -webkit-transform .4s linear;
        -webkit-transform: rotate3d(1, 0, 0, -90deg);
                transform: rotate3d(1, 0, 0, -90deg);
        z-index: 1;
}
.c-cloud .m-back .u-photo.rotate .u-front {
      -webkit-transition: -webkit-transform .4s linear;
      transition: -webkit-transform .4s linear;
      transition: transform .4s linear;
      transition: transform .4s linear, -webkit-transform .4s linear;
      -webkit-transform: rotate3d(1, 0, 0, -90deg);
              transform: rotate3d(1, 0, 0, -90deg);
}
.c-cloud .m-back .u-photo.rotate .u-back {
      -webkit-transition: -webkit-transform .4s .4s linear;
      transition: -webkit-transform .4s .4s linear;
      transition: transform .4s .4s linear;
      transition: transform .4s .4s linear, -webkit-transform .4s .4s linear;
      -webkit-transform: rotate3d(1, 0, 0, -180deg);
              transform: rotate3d(1, 0, 0, -180deg);
}
.c-cloud .m-front {
    pointer-events: auto;
    -webkit-animation: fadeInCloud .8s 1.6s;
            animation: fadeInCloud .8s 1.6s;
    -webkit-animation-fill-mode: backwards;
            animation-fill-mode: backwards;
    background-color: rgba(41, 41, 79, 0.6);
    -webkit-transform: translateZ(500px);
            transform: translateZ(500px);
    z-index: 9;
}
.c-cloud .m-front:before {
      width: 1px;
      height: 100%;
      vertical-align: middle;
      content: '';
      display: inline-block;
}
.c-cloud .m-front .m-content {
      display: inline-block;
      vertical-align: middle;
      pointer-events: none;
}
.c-cloud .m-front .u-qr {
      margin-top: 133px;
      background-color: white;
      padding: 8px;
      display: inline-block;
}
.c-cloud .m-front .u-qr img {
        display: block;
        width: 322px;
        height: 322px;
}
.c-cloud .m-front .u-title {
      font-size: 49px;
      color: #3df6ff;
      font-weight: bold;
}



